import React from 'react';
import {
    FormLayout,
    Button,
    Cell,
    Group,
    FormLayoutGroup,
    Textarea,
    Separator,
    Avatar,
    Spinner, CardGrid, Card, File, Snackbar, Div, PanelSpinner
} from "@vkontakte/vkui";
import Icon24Camera from '@vkontakte/icons/dist/24/camera';
import {BACKEND} from "../func/func";
import Spin from '../elements/spinner'
import fetchJsonp from "fetch-jsonp";
import bridge from "@vkontakte/vk-bridge";


class FindModelMaster extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            activeMaster: '',
            body: '',
            isLoaded: false,
            isActive: false,
            error: '',
            loading: false
        };
    }
    componentDidMount() {
        this.getToken();
        fetch(BACKEND.masters.vkuid+this.props.user.vkUid)
            .then(res => res.json())
            .then(activeMaster => {
                this.setState({activeMaster: activeMaster[0]});
                fetch(BACKEND.findModel.onMasterId+activeMaster[0]._id)
                    .then(res => res.json())
                    .then(find => {
                        if (find.length > 0) {
                            this.setState({body:find[0].body, error: '', isLoaded: true, isActive: true, loadFind: find[0]});
                        } else {
                            let error = <Cell>    </Cell>;
                            this.setState({error: error, isLoaded: true});
                        }
                    });
            });
    }

    openSnack (text) {
        const blueBackground = {
            backgroundColor: 'var(--accent)'
        };
        if (this.state.snackbar) return;
        this.setState({ snackbar:
                <Snackbar
                    layout="vertical"
                    onClose={() => this.setState({ snackbar: null })}
                >
                    {text}
                </Snackbar>
        });
    }

    getToken = () => {
        bridge.send("VKWebAppGetAuthToken", {"app_id": 7170938, "scope": "photos"})
            .then(data => {
                //console.log(' '+data.access_token);
                this.getUploadServer(data.access_token);
                //this.setState({token: data.access_token})
            })
            .catch(error => console.log(error))
    }

    getUploadServer = (token) => {
        bridge.send("VKWebAppCallAPIMethod", {
            "method": "photos.getUploadServer",
            "params": {"group_id": "193179174","album_id": "269622026", "v":"5.103", "access_token": token}})
            .then(result => {
                //console.log(result.response.upload_url);
                this.setState({uploadUrl: result.response.upload_url, token: token});
            })
            .catch(e => console.log(e))

    };

    uploadPhoto = () =>{
        this.setState({loading: true});
        const formData = new FormData();
        let selectedFile = document.getElementById('input').files[0];
        formData.append('master', this.state.activeMaster.firstname+' '+this.state.activeMaster.lastname );
        formData.append('uploadUrl', this.state.uploadUrl);
        formData.append('token', this.state.token);
        formData.append('file1', selectedFile);
        fetch(BACKEND.vkapi.uploadPhoto, {
            method: 'POST',
            body: formData
        })
            .then(res => res.json())
            .then(response => {
                this.openSnack(response.message);
                //console.log(response);
                fetchJsonp(response.saveUrl, {
                    mode: 'no-cors',
                    method: 'GET'
                })
                    .then(result => result.json())
                    .then(result => {
                        //console.log(result);
                        let newImg = result.response[0].sizes[2].url;
                        let imgArr = this.state.images;
                        imgArr.unshift(newImg);
                        this.setState({images: imgArr});
                        let data = {
                            findId: this.state.loadFind,
                            newImg: newImg
                        };
                        //console.log(data);
                        fetch(BACKEND.vkapi.savePhotoFindModels, {
                            mode: 'cors', // no-cors, cors, *same-origin
                            cache: 'no-cache', // *default, no-cache, reload, force-cache, only-if-cached
                            credentials: 'same-origin', // include, *same-origin, omit
                            headers: {
                                'Content-Type': 'application/json',
                                // 'Content-Type': 'application/x-www-form-urlencoded',
                            },
                            method: 'POST',
                            body: JSON.stringify(data),
                            redirect: 'follow', // manual, *follow, error
                            referrer: 'no-referrer', // no-referrer, *client
                        })
                            .then(result => result.json())
                            .then(result => {
                                this.openSnack(result.message);
                                console.log(result.url);
                                let loadFind = this.state.loadFind;
                                loadFind.images.push(result.url);
                                this.setState({loading: false, loadFind: loadFind});
                            })
                            .catch(error => this.openSnack(error))

                    })
                    .catch(error => this.openSnack(error))
            })
            .catch(error => this.openSnack(error))
    };

    handleChange = (event) => {
        let {name, value} = event.target;
        this.setState({[name]: value});
    }
    save=()=>{
        let find = {
            masterId: this.state.activeMaster._id,
            body: this.state.body,
            location: this.state.activeMaster.location,
            firstname: this.state.activeMaster.firstname,
            lastname: this.state.activeMaster.lastname,
            avatarLink: this.state.activeMaster.avatarLink
        };
        if (this.state.isActive === true) {
            let f =this.state.loadFind;
            f.body = this.state.body;
            this.setState({loadFind: f});
            this.patchData(BACKEND.findModel.new+this.state.loadFind._id, find);
        } else {
            this.postData(BACKEND.findModel.new, find);
        }
    }
    patchData(url = '', data = {}) {
        return fetch(url, {
            method: 'PATCH', // *GET, POST, PUT, DELETE, etc.
            mode: 'cors', // no-cors, cors, *same-origin
            cache: 'no-cache', // *default, no-cache, reload, force-cache, only-if-cached
            credentials: 'same-origin', // include, *same-origin, omit
            headers: {
                'Content-Type': 'application/json',
                // 'Content-Type': 'application/x-www-form-urlencoded',
            },
            redirect: 'follow', // manual, *follow, error
            referrer: 'no-referrer', // no-referrer, *client
            body: JSON.stringify(data), //    body     "Content-Type"
        })
            .then(response=>response.json()) //  JSON   Javascript 
            .then(result=>{
                console.log(result);
                this.props.popout();
            })
    }
    postData(url = '', data = {}) {
        //      *
        fetch(url, {
            method: 'POST', // *GET, POST, PUT, DELETE, etc.
            mode: 'cors', // no-cors, cors, *same-origin
            cache: 'no-cache', // *default, no-cache, reload, force-cache, only-if-cached
            credentials: 'same-origin', // include, *same-origin, omit
            headers: {
                'Content-Type': 'application/json',
                // 'Content-Type': 'application/x-www-form-urlencoded',
            },
            redirect: 'follow', // manual, *follow, error
            referrer: 'no-referrer', // no-referrer, *client
            body: JSON.stringify(data), //    body     "Content-Type"
        })
            .then(response=>response.json()) //  JSON   Javascript 
            .then(result=>{
                console.log(result);
                this.setState({loadFind: result, isActive: true})
            })

    }
    render(){
        if (this.state.isLoaded === false){
            return (<Spin />)
        } else {
            return (
                <React.Fragment>
                {
                    this.state.loading ? <Div><Cell multiline> ...  </Cell><PanelSpinner /></Div> :
                        <FormLayout>
                            <FormLayoutGroup>
                                <Cell> / </Cell>
                                <Textarea
                                    name={'body'}
                                    bottom={this.state.body ? '' : ',     '}
                                    value={this.state.body}
                                    onChange={this.handleChange}
                                />
                                <File
                                    top="   "
                                    before={<Icon24Camera />}
                                    size="l"
                                    onChange={this.uploadPhoto}
                                    id="input"
                                >
                                     
                                </File>
                            </FormLayoutGroup>
                            <Button size="xl" onClick={this.save}></Button>
                            <Separator style={{ margin: '12px 0' }} />
                            {
                                this.state.isActive &&
                                <CardGrid>
                                    <Cell>  :</Cell>
                                    <Card size='l'>
                                        <Cell expandable
                                              photo="https://pp.userapi.com/c841034/v841034569/3b8c1/pt3sOw_qhfg.jpg"
                                              description={'     '}
                                              before={<Avatar src={this.state.loadFind.avatarLink} size={50}/>}
                                              size="l"
                                        >{this.state.loadFind.firstname} {this.state.loadFind.lastname}
                                        </Cell>
                                        <Cell multiline>
                                            {this.state.body}
                                        </Cell>
                                        {
                                            Array.isArray(this.state.loadFind.images) &&
                                            this.state.loadFind.images.map(img=>{
                                                return <Avatar src={img} size={50}/>
                                            })
                                        }
                                    </Card>
                                </CardGrid>
                            }
                            {this.state.snackbar}
                        </FormLayout>
                }
                </React.Fragment>
            );
        }

    }
}

export default FindModelMaster;